.TodoCounter {
    font-size: 20px;    
    padding: 20px;
    text-align: center;
    margin: 10px;
   
}

.PrintTodoButton {
   
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    
    bottom: 24px;
    right: 24px;
    font-weight: bold;
   
    display: flex;

    align-items: center;
    height: 64px;
    width: 64px;

    
    z-index: 1;
}