.TodoSearch{
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 14px;    
    height: 64px;
    
    font-size: 24px;
    text-align: center;
    font-family: 'Alegreya';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);

    
    

}

.TodoSearch::placeholder{
    color: #A5A5A5;
    font-family: 'Alegreya';
    font-weight: 400;    
}

.TodoSearch:focus{
    outline-color: #64DAFA;    
}

.TodoSearh:disabled {
    opacity: 25%;
}