.LoadingTodo-container {
    
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);    
}

.LoadingTodo-text {
    
    margin: 0 24px;    
    width: calc(100% -120px);
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    border-radius: 2px;
    height: 48px;
    width: 48px;
}

.LoadingTodo.completeIcon {
    position: absolute;
    left: 12px;
}

.LoadingTodo-deleteIcon {
    position: absolute;
    top: -24px;
    right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200,199,199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;    
}

@keyframes loadingAnimation {
    0% {background-position: 0% 50%;}
    50% {background-position: 100% 50%;}
    100% {background-position: 0% 50%;}
}


